<template>
  <div id="pay_box">
    <div style="margin:20px;background:#fff;">
      <div>
        <div class="online_title">{{ payTitle }}</div>
        <div style="font-weight:bold;margin-top:10px;font-size:18px;">¥{{ statusInfo.totalPrice | point }}</div>
        <img :src="code"
             style="width:100%;"
             alt="">
        <div style="display:flex;padding:0px 40px;justify-content:space-between;">
          <img src="../../assets/two/zhifb.png"
               class="w50"
               alt="">
          <img src="../../assets/two/weix.png"
               class="w50"
               alt="">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { PostBusinessHotelSoftCostBill } from '@/api/api_soft.js';
import { Toast } from 'vant';
export default {
    data() {
        return {
            payTitle: '请扫码支付',
            currentInfo: null,
            status: '0',
            statusInfo: null,
            code: null,
            uploader: null,
            photos: []
        };
    },
    mounted() {
        this.timer = setInterval(this.getIsPay, 10000);
    },
    beforeDestroy() {
        clearInterval(this.timer);
    },
    created() {
        this.getIsPay();
        this.code = 'data:image/png;base64,' + this.$route.query.code;
        this.$store.commit('setHeader', '扫码付款');
    },
    methods: {
        // 查询付款二维码
        getIsPay() {
            PostBusinessHotelSoftCostBill({ hotelSoftCostBillId: this.$route.query.hotelSoftCostBillId }).done((res) => {
                this.statusInfo = res.data;
                if (res.data.payment > 0) {
                    clearInterval(this.timer);
                    setTimeout('window.history.go(-2)', 900);
                }
            });
        }
    }
};
</script>
<style scoped>
#pay_box {
    font-size: 14px;
}
.online_title {
    background: linear-gradient(270deg, #ff7d41 0%, #ff8e5a 100%);
    line-height: 40px;
    color: #fff;
    border-radius: 5px 5px 0 0;
    text-align: center;
    letter-spacing: 12px;
    text-indent: 0.6em;
}
.w50 {
    width: 50%;
    height: 45px;
}
.pay_button {
    width: 90%;
    background: #ff7d41;
    color: #fff;
    margin: 30% 20px 0;
}
.url_box {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-size: 14px;
    color: #646566;
    padding: 0 0 0 16px;
    background: #fff;
    align-items: center;
}
/deep/ .van-cell {
    border-bottom: 1px solid #eee;
}
</style>



